<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Information') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>

                    </div>
                    <div class="modal-body">
                        <template>
                            <div class="listing" id="traininglist">
                                <div class="table-responsive">
                                    <table class="table table-bordered dt-responsive nowrap mt-0 w-100"
                                        id="tblFollowUpList">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th class="text-center" style="width:30px;">
                                                    {{ $t("S.No") }} 
                                                </th>
                                                <th>
                                                    {{ $t("Message") }} 
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-if="invoiceLogs.length === 0">
                                                <td colspan="6" class="no_records no-record">
                                                    {{ $t("NoRecordfound") }}
                                                </td>
                                            </tr>
                                            <tr v-else v-for="(log, index) in invoiceLogs" :key="index">
                                                <td class="text-center">
                                                    {{ index + 1 }}
                                                </td>
                                                <td class="wordbreak">
                                                    <span>{{ log.message }}</span>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </template>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from './../../../services/DataService';
export default {
    props: {
        BillId: {
            type: Object,
            required: true
        }
    },
    components: {},
    data() {
        return {
            isLoading: false,
            invoiceLogs: [],
        }
    },
    created: function () {
        var vm = this;
        vm.FetchData();
    },
    methods: {
        FetchData: function () {
            var vueObj = this;
            vueObj.isLoading = true;
            var params = `exportId=${vueObj.BillId}`;
            DataServices.GetExportErrorLogs(params).then((response) => {
                if (response.data) {
                    vueObj.invoiceLogs = response.data;
                }
                else {
                    vueObj.invoiceLogs = [];
                }
                vueObj.isLoading = false;
            })
        },
        Close: function () {
            var vm = this;
            vm.$parent.IsLogError = false;
        }
    }
}
</script>