    AllowNumberWithDotOnBlur() {
      // Remove invalid characters and ensure only one dot is allowed
      let value = this.inputValue;

      // Allow only numbers and a single dot
      value = value.replace(/[^0-9.]/g, "");

      // Ensure only one dot exists in the value
      const parts = value.split(".");
      if (parts.length > 2) {
        value = `${parts[0]}.${parts.slice(1).join("")}`;
      }

      // Update the input value with the validated result
      this.inputValue = value;
