FetchData: async function () {
    debugger
    var vueObj = this;
    vueObj.BillListing = [];
    var url = `pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&clientId=${vueObj.UserId}`;

    await DataService.GetInvoiceListing(url).then(function (response) {

        if (response.data != "" && response.data != null) {

            if (response.data.length > 0) {

                vueObj.BillListing = response.data;

                for (var row in vueObj.BillListing) {

                    vueObj.BillListing[row].isCheckBoxDisabled = false;

                    // Disable checkbox for specific status codes
                    if (vueObj.BillListing[row].STATUS_CODE == "RLSTOXERO"
                        || vueObj.BillListing[row].STATUS_CODE == "RLSTOWORKDAY"
                        || vueObj.BillListing[row].STATUS_CODE == "CLBPAD"
                        || vueObj.BillListing[row].STATUS_CODE == "CLBCNCL") {

                        vueObj.BillListing[row].isCheckBoxDisabled = true;
                    }

                    if (vueObj.BillListing[row].STATUS_NAME === "Sent") {

                    const total = vueObj.BillListing[row].BILL_NET_TOTAL;

                    if (total < 0) {
                        vueObj.BillListing[row].STATUS_NAME = "Paid";
                    }
                    else if (total === 0) {
                        vueObj.BillListing[row].STATUS_NAME = "Bill Generated";
                    }
                    else {
                        vueObj.BillListing[row].STATUS_NAME = "UnPaid";
                    }
                    }
                }

                vueObj.TotalRecords = response.data[0].TOTAL_RECORDS;
                vueObj.TotalPages = Math.ceil(
                    vueObj.TotalRecords / vueObj.pageSize
                );
                vueObj.CurrentPage = vueObj.pageNum;
                vueObj.noRecord = false;

            } else {

                vueObj.noRecord = true;
                vueObj.TotalRecords = 0;
                vueObj.BillListing = [];
            }

        } else {
            vueObj.noRecord = true;
            vueObj.TotalRecords = 0;
            vueObj.BillListing = [];
        }

    });
},

